@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500;600;700&display=swap);
.author{margin-left:48px;margin-right:48px;padding-top:16px}

.footer{background-color:#0e0e0e;padding:20px 48px;margin-top:48px;color:#ffffff}

.header{width:100vw;padding:0 52px;font-size:16px;box-sizing:border-box;border-bottom:1px solid #2b2b2b}.header__logo{display:inline-block;margin-right:32px;padding-left:32px;padding-right:32px;background-color:#2b2b2b;font-weight:700;text-decoration:none;line-height:48px}.header__logo:hover{background-color:#272727;color:#565ece;cursor:pointer;transition:all 0.2s}


.background{width:100vw;height:50vh;background-color:#dddddd;background-size:cover;margin-top:32px}.background__credits{text-align:right;padding:16px;margin-left:48px;margin-right:48px}

.definition{background-color:#272727;padding:3px 6px}

.image{max-width:100vw;margin-top:24px;margin-bottom:24px}.image-container{width:100vw;text-align:center}

.subtitle{font-size:24px;font-weight:600;margin-top:64px;margin-left:48px;margin-right:48px;margin-bottom:16px;background-color:#5a20b8;color:#ffffff;display:inline-block;padding:7px 14px;border-right:10px solid #42158b}

.text{font-size:16px;font-weight:400;line-height:1.5;margin-left:48px;margin-right:48px;margin-top:12px;margin-bottom:12px;text-align:justify}

.title{font-family:'Work Sans', sans-serif;font-size:40px;font-weight:700;color:#f0f0f0;margin-left:48px;margin-right:48px;margin-top:48px}.title2{font-family:'Work Sans', sans-serif;font-size:32px;font-weight:700;color:#949494;margin-left:48px;margin-right:48px;margin-top:8px;margin-bottom:48px}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body{background-color:#141414;color:#e9e9e9}ul{list-style-type:square;list-style-position:inside;list-style:inside;margin-left:8px}.error404{font-size:60px;font-weight:500;text-align:center;margin-top:120px}.error404__link{display:block;color:inherit;font-size:24px;margin-top:32px}a{color:#7674d4}

@font-face{font-family:'JetBrains Mono';src:url("https://cdn.jsdelivr.net/gh/JetBrains/JetBrainsMono/web/woff2/JetBrainsMono-Regular.woff2") format("woff2"),url("https://cdn.jsdelivr.net/gh/JetBrains/JetBrainsMono/web/woff/JetBrainsMono-Regular.woff") format("woff"),url("https://cdn.jsdelivr.net/gh/JetBrains/JetBrainsMono/ttf/JetBrainsMono-Regular.ttf") format("truetype");font-weight:400;font-style:normal}body{font-family:"Work Sans", sans-serif;font-weight:400}

.cards{padding:16px 24px;padding-top:0}.card{position:relative;background-color:#1f1f1f;display:inline-block;height:100px;width:300px;padding:16px 24px;margin:16px 24px;margin-top:0;margin-bottom:32px;cursor:pointer;border-bottom:3px solid #1f1f1f;text-decoration:none}.card:hover{background-color:#2b2b2b;border-bottom:3px solid gray;transition:all 0.2s}.card__title{display:block;color:#7880ec;font-size:16px;font-weight:500}.card__subtitle{display:block;color:#acacac;font-size:22px;font-weight:500;margin-top:4px}.card__date{position:absolute;right:24px;bottom:16px;color:#8a8a8a;font-weight:500}@media screen and (max-width: 900px){.card{width:calc( 100vw - 96px - 48px)}}

.code{margin:16px 48px;font-family:'JetBrains Mono', monospace;padding:12px;background-color:#282a36}button{border:none;outline:none}

